.box{
  border: 1px solid black;
  border-radius: 10%;
  width: 500px;
}

.main{
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  text-align: center;
}

.win{
  border: 10px solid green;
  color: green;
}

.lose{
  border: 10px solid red;
  color: red;
}

.tie{
  border: 10px solid black;
  color: black;
}


